@import"https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap";*{margin:0;padding:0;border:0;outline:0;box-sizing:border-box;text-decoration:none}:root{--color-bg: #1f1f38;--color-bg-variant: #ff1744;--color-primary: #ef5350;--color-primary-variant: rgba(255, 205, 210, 0.4);--color-white: #fff;--color-light: rgba(255, 255, 255, 0.6);--color-yellow: #ffeb3b;--color-blue: #2196f3;--container-width-lg: 75%;--container-width-md: 86%;--container-width-sm: 90%;--transition: all 400ms ease}html{scroll-behavior:smooth}::-webkit-scrollbar{display:none}body{font-family:"Poppins",sans-serif;background:var(--color-bg);color:var(--color-white);line-height:1.7;background-image:url(../src/assets/bg-texture.png)}.container{width:var(--container-width-lg);margin:0 auto}h1,h2,h3,h4,h5{font-weight:500}h1{font-size:2.5rem}section{margin-top:8rem}section>h2,section h5{text-align:center;color:var(--color-light)}section>h2{color:var(--color-primary);margin-bottom:3rem}.text-light{color:var(--color-light)}a{color:var(--color-primary);transition:var(--transition)}a :hover{color:var(--color-white)}.btn{width:max-content;display:inline-block;color:var(--color-primary);padding:.75rem 1.2rem;border-radius:.4rem;cursor:pointer;border:1px solid var(--color-primary);transition:var(--transition);text-align:center}.btn-sm{padding:.5rem 1rem;font-size:.8rem;vertical-align:middle;height:fit-content}.btn:hover{background:var(--color-white);color:var(--color-bg);border-color:rgba(0,0,0,0)}.btn-primary{background:var(--color-primary);color:var(--color-light)}.btn-secondary{background:var(--color-primary-variant);color:var(--color-light)}.fluid{width:100%}img{display:block;width:100%;object-fit:cover}.swiper-pagination-bullet{background:var(--color-white) !important;opacity:.2}.swiper-pagination-bullet-active.swiper-pagination-bullet{background:var(--color-blue) !important;opacity:1}div[class*=swiper-button]{color:var(--color-primary-variant) !important}ul>li{list-style-type:none}ul.list-tags{display:flex;flex-wrap:wrap}ul.list-tags>li{font-size:.9rem;color:var(--color-light)}ul.list-tags>li::after{content:"•";margin:0 .3rem;vertical-align:middle}ul.list-tags>li:last-child::after{content:""}.hidden{display:none}.overflow-hidden{overflow:hidden}@media screen and (max-width: 1024px){.container{width:var(--container-width-md)}section{margin-top:6rem}}@media screen and (max-width: 600px){.container{width:var(--container-width-sm)}section>h2{margin-bottom:2rem}}